@font-face {
  font-family: "Roboto";
  src: local("Roboto"),
    url("../fonts/roboto-v20-cyrillic-regular.woff2") format("woff2"),
    url("../fonts/roboto-v20-cyrillic-regular.woff") format("woff");
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}

@font-face {
  font-family: "Playfair Display";
  src: local("Playfair Display"),
    url("../fonts/playfair-display-v20-cyrillic-regular.woff2") format("woff2"),
    url("../fonts/playfair-display-v20-cyrillic-regular.woff") format("woff");
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}
