body {
  display: flex;
  flex-direction: column;
  margin: 0;
  padding: 0;
  height: 100vh;
  min-width: 288px;
  font-family: "Roboto", "Verdana", sans-serif;
  font-weight: 400;
  color: green;
  background-color: #fdfdfb;
}

main {
  flex: 1 0 auto;
}

header,
footer {
  flex-shrink: 0;
}

.visually-hidden:not(:focus):not(:active),
input[type=checkbox].visually-hidden,
input[type=radio].visually-hidden,
.sr-only {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

a {
  text-decoration: none;
}

img {
  max-width: 100%;
  height: auto;
}

.btn {
  display: inline-block;
  box-sizing: border-box;
  padding: 26px 38px;
  font-family: "roboto", "Verdana", sans-serif;
  font-weight: 500;
  font-size: 12px;
  line-height: 14px;
  letter-spacing: 0.05em;
  color: #ffffff;
  text-align: center;
  text-transform: uppercase;
  appearance: none;
  cursor: pointer;
}
.btn--grey {
  background-color: #444240;
}
.btn--beige {
  background-color: #d4b098;
}

.modal {
  position: fixed;
  top: 50%;
  left: 50%;
  z-index: 30;
  display: none;
  opacity: 0;
  transform: translate(-50%, -50%);
  transition: all 0.25s;
}
.modal--open {
  display: block;
  opacity: 1;
}

@keyframes dropdown {
  from {
    height: 0;
    opacity: 0;
  }
  to {
    height: 100%;
    opacity: 1;
  }
}
