.burger {
  padding: 0;
  border: none;
  box-sizing: content-box;
  font: inherit;
  color: inherit;
  background-color: transparent;
  cursor: pointer;
  position: relative;
  z-index: 15;
  font: inherit;
  color: inherit;
  text-transform: none;
  cursor: pointer;
  transition-duration: 0.075s;
  transition-timing-function: ease-in-out;
  overflow: visible;
}
.burger:hover, .burger:focus {
  outline: none;
}
.burger:active {
  outline: 0;
}
.burger span {
  position: absolute;
  top: 50%;
  margin-top: -1px;
  display: block;
  width: 100%;
  height: 1px;
  background-color: #1b1a17;
  transition-property: transform;
  transition-duration: 0.075s;
  transition-timing-function: ease-in-out;
}
.burger::before, .burger::after {
  position: absolute;
  content: "";
  display: block;
  width: 100%;
  height: 1px;
  background-color: #1b1a17;
  transition-property: transform;
  transition-duration: 0.075s;
  transition-timing-function: ease-in-out;
}
.burger::before {
  top: 0;
  transition: top 0.075s ease-in-out, opacity 0.075s ease-in-out;
}
.burger::after {
  bottom: 0;
  transition: bottom 0.075s ease-in-out, opacity 0.075s ease-in-out;
}
.burger:hover span, .burger:focus span {
  background-color: #818d92;
}
.burger:hover::before, .burger:hover::after, .burger:focus::before, .burger:focus::after {
  background-color: #818d92;
}
.burger:active span {
  background-color: #818d92;
  opacity: 0.3;
}
.burger:active::before, .burger:active::after {
  background-color: #818d92;
  opacity: 0.3;
}
.burger:active::before {
  top: 50%;
  transform: translateY(-50%);
}
.burger:active::after {
  bottom: 50%;
  margin: 1px;
}
.burger--active span {
  transform: rotate(45deg);
  transform-origin: center;
}
.burger--active::before {
  top: 50%;
  margin: -1px;
  transform: rotate(-45deg);
  transform-origin: center;
}
.burger--active::after {
  bottom: 50%;
  margin: 1px;
  opacity: 0;
}

.search {
  display: inline-block;
  width: auto;
  margin: 0;
  padding: 0;
  font-size: inherit;
  line-height: inherit;
  font-family: inherit;
  vertical-align: middle;
  color: inherit;
  white-space: normal;
  background: none;
  border: 0;
}

.search__field {
  border: none;
  outline: none;
  transition: all 0.15s ease-in-out;
}
.search__field::-webkit-search-cancel-button {
  display: none;
}

.search__icon {
  fill: #1b1a17;
  cursor: pointer;
}
.search__icon:hover, .search__icon:focus {
  fill: #818d92;
}
.search__icon:active {
  fill: #818d92;
  fill-opacity: 0.5;
}

