  position: relative;
  display: flex;
  flex-wrap: wrap;
  align-items: flex-start;
  padding-top: 31px;
  padding-bottom: 31px;
}

.main-nav__burger {
  width: 18px;
  height: 16px;
  margin-top: 4px;
  margin-right: 24px;
}

.main-nav__logo-link {
  position: relative;
  z-index: 15;
  width: 105px;
  height: 27px;
  fill: none;
  transition: all 0.15s ease-in-out;
}
.main-nav__logo-link[href]:hover, .main-nav__logo-link[href]:focus {
  opacity: 0.5;
}
.main-nav__logo-link[href]:active {
  opacity: 0.3;
}

.main-nav__user-section {
