  stroke: #818d92;
}
.main-nav__link--likes:active {
  stroke: #818d92;
  opacity: 0.5;
}
.main-nav__link--cart {
  fill: #1b1a17;
}
.main-nav__link--cart:hover, .main-nav__link--cart:focus {
  fill: #818d92;
}
.main-nav__link--cart:active {
  fill: #818d92;
  fill-opacity: 0.5;
}
.main-nav__link:last-of-type {
  margin-right: 0;
}

.main-nav__menu {
  position: absolute;
  content: "";
  z-index: 10;
  top: 0;
  left: 2%;
  display: none;
}
.main-nav__menu--shown {
  display: block;
  animation: dropdown 0.15s ease-in-out;
}

@media (max-width: 767px) {
  .main-nav__user-section {
    position: relative;
    z-index: 15;
  }
}
.main-menu {
  padding: 70px 27px 27px 47px;
  background-color: #fdfdfb;
}

.main-menu__list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
  flex-direction: column;
}

.main-menu__link {
  font-family: "Roboto", "Verdana", sans-serif;
  font-weight: 400;
  display: block;
  padding: 5px 0;
  border-bottom: 1px solid #636261;
  font-size: 16px;
  line-height: 20px;
  color: #555248;
  transition: all 0.15s ease-in-out;
  outline: none;
}
.main-menu__link:hover, .main-menu__link:focus {
  opacity: 0.5;
}
.main-menu__link:active {
  opacity: 0.3;
}
.main-menu__link--active {
  color: #a69876;
}

@media (max-width: 767px) {
  .main-menu {
    left: 0;
    box-sizing: border-box;
    padding: 70px 0 27px;
    width: 100vw;
  }

  .main-menu__link {
    padding: 15px 2%;
    text-align: center;
  }
}